.admin {
  height: 100%;
  width: 100%;
}
.admin .header {
  height: 100%;
  width: 100%;
  background-color: #aae7ff;
}
.admin .logo {
  height: 100%;
  width: 300px;
  padding: 0px 50px;
  display: flex;
  float: left;
}
.admin .logo .img {
  display: inline-block;
  height: 60px;
  width: 60px;
  margin: 0px 15px;
}
.admin .logo .h1 {
  display: inline-block;
  font-size: 20px;
}
.admin .dblist {
  align-content: center;
  float: right;
  margin: 0px 60px 0px 20px;
  height: 60px;
  width: 350px;
}
.admin .search {
  align-items: center;
  display: flex;
  height: 30px;
  width: 100%;
  padding: 0px 10px 0px 10px;
  background-color: #aae7ff;
}
.admin .collapsed {
  align-items: center;
  display: flex;
  height: 30px;
  width: 100%;
  padding: 0px 10px 0px 10px;
  background-color: #aae7ff;
}
.admin .collapsed .collapsedicon {
  float: right;
}
.admin .tablelistscroll {
  padding: 10px 0px;
  height: calc(100vh - 140px);
  overflow-y: auto;
  overflow-x: hidden;
  background-color: #ffffff;
}
.admin .tablelistscroll .loading-container {
  position: absolute;
  bottom: 40px;
  width: 100%;
  text-align: center;
}
.admin .tablelistscroll .listitem {
  padding: 0;
  margin: 0;
  width: 100%;
}
.admin .tablelistscroll .listitem .tooltip {
  background-color: #d4d4d4;
}
.admin .tablelistscroll .listitem .listitemmeta {
  width: 100%;
  padding: 0px 10px 0px 10px;
  cursor: pointer;
}
.admin .tablelistscroll .listitem :hover {
  background-color: #c2f3ff;
}
.admin .tableinfo {
  padding: 15px 0px 0px 0px;
  margin: 0px 50px;
  height: 50px;
  text-align: left;
  line-height: 20px;
}
.admin .tableinfo .tableinfotext {
  font-size: 20px;
  color: #000;
}
.admin .logoutbt {
  align-content: center;
  float: right;
  height: 100%;
  width: 78px;
}
.admin .table {
  overflow: hidden;
  margin-top: 10px;
  box-shadow: 0px 0px 10px #d6d6d6;
}
.admin .footer {
  height: 100%;
  width: 100%;
  text-align: center;
}
.admin .tableloading-container {
  z-index: 10;
  position: absolute;
  height: calc(100vh - 140px);
  width: 100%;
  padding: 100px 0 0 0;
  background-color: rgba(255, 255, 255, 0.5);
}
